@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
* {box-sizing: border-box;}
body {
  margin: 0;
  padding: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size:20px;
  background: rgb(2, 106, 224);
  /*background: linear-gradient(330deg, rgba(29,80,250,1) 18%, rgba(0,236,231,1) 99%);
  */color:white;
}

body, html {
  height: 100%;
  width: 100%;
}

#root {
  height:100%;
  width:100%; 
  padding:0 20px;
  border-radius:5px;
  overflow: hidden;
}
/*--------Header.js styles----------*/
.header-style {
  background:rgba(0,0,0,0);
  height:80px ;
  width:100% ;
  padding:20px ;
  border-radius: 12px;
  margin-top:1vh;
}

.header {
  text-align: center;
  margin: 0;
  color: white;
  font-size: 48px;
}
/*---------Search.js styles--------*/
.searchStyle {
  margin:8vh auto 0;
  color:white;
  font-size: 25px;
  text-align: center;
}
.searchButton {
  margin:10px auto;
  display: block;
  height: 45px;
  width: 285px;
  border:1px solid white;
  border-radius: 2px;
  color: white;
  background-color: #ffffff25;
  -webkit-transition: all 0.3s cubic-bezier(.08,.52,.52,1);
  transition: all 0.3s cubic-bezier(.08,.52,.52,1);
}
.city {
  font-size: 20px;
  height: 45px;
  width: 285px;
  margin-top:10px;
  -webkit-transition: all 0.3s cubic-bezier(.08,.52,.52,1);
  transition: all 0.3s cubic-bezier(.08,.52,.52,1);
}
.coord {
  margin:10px 0;
}
.searchButton:hover {
  background-color: #ffffff66;
  cursor: pointer;
}
.searchButton span {
  text-align: left;
}
.searchStyle div div input {
  float: right;
  border:none;
  height: 45px;
}
.searchStyle input {
  border:none;
  padding:0 10px;
  border-radius: 2px;
}

.findBtn:hover {
  background-color:#ffffff66 !important ;

}
/* --------Temp.js styles----------*/
.tempStyle {
  
  color:white;
  display: flex;
  flex-direction: column;
  width: 75%;
  margin:0 auto;
}

.tempStyle .temp {
  font-size: 25vh;
  font-weight: 100;
  text-align: center;
  flex:3 1;
  margin-bottom: 7px;
}

.tempStyle .others {
  font-size: 22px;
  text-align: center;
  flex: 1 1;
  margin-bottom: 20px;
}

.details {
  display:flex;
  justify-content: space-around;
  background: #ffffff25;
  border-radius: 10px;
  height:16vh;
  margin:0 auto 20px;
  padding:0px 0;
} 



.details div {
  text-align: center;
  width:auto;
  padding: 1vh 0; 
}

.details div i {
  font-size: 40px;
  padding:10px 0;
}

input[type=number] {
  width:80px;
  height:30px;
  
}

@media screen and (min-width:840px) {
  .details {
    width:800px;
  }
}

@media screen and (max-width:540px) {
  #root {padding:0 10px;}
  .header {font-size: 36px;}
  .city, .searchButton {
    width: 75vw;
  }
  h2 {
    margin:0 !important;
    font-size: 20px;
  }
  .tempStyle .temp {
    font-size: 130px;
  }
  .details div i {
    font-size: 24px;
  }
  .details div span {
    font-size: 16px;
  }
}


